import request from "../helpers/request";

const URL={
  GetBlog:'/blog',
  BlogArticle:'/blog/:blogId',
  Create:'/blog',
  update:'/blog/:blogId',
  Delete:'/blog/:blogId'
}
export default {
  //获取用户博客列表
  getbloglist({userId,page = 1} = {page:1}){
    return request(URL.GetBlog,'GET',{userId,page})
  },
  //获取首页博客列表
  getAllbloglist({page = 1} ={page:1}){
    return request(URL.GetBlog,'GET',{page,atIndex:true})
  },
  //获取博客文章
  getBlogArticle({blogId}){
    return request(URL.BlogArticle.replace(':blogId',blogId))
  },
  //创建博客
  createBlog({title ='',content = '',description = '',atIndex =true}){
    return request(URL.Create,'POST',{title,content,description,atIndex})
  },
  //删除博客
  deleteBlog({blogId}){
    return request(URL.Delete.replace(':blogId',blogId),'DELETE')
  },
  //修改博客
  updateBlog({blogId},{title,content,description,atIndex}){
    return request(URL.update.replace(':blogId',blogId),'PATCH',{title,content,description,atIndex})
  }
}



